<template>
    <div class="average">
        <my-title><i class="fa-solid fa-arrow-trend-up"></i> average</my-title>
        <div class="own__wrapper">
            <div>
                <div class="average__value">{{ average }}&deg;</div>
                <div class="average__value-desc">differs from the average daily temperature maximum</div>
            </div>
            <div class="average__block-info">
                <div class="average__block-elem">
                    <div class="average__elem">Today</div>
                    <div class="average__elem">Max.: {{ Math.round(today.tempmax )}}&deg;</div>
                </div>
                <div class="average__block-elem">
                    <div class="average__elem">Average</div>
                    <div class="average__elem">Max.: {{ Math.round(today.tempmax / 2 )}}&deg;</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            today: {
                type: Object,
                required: true
            }
        },
        computed: {
            average() {
                return Math.round((this.today.tempmax + this.today.tempmin) / 2)
            }
        }
    }
</script>

<style scoped>
.average {
    padding: 10px 10px 0 10px;
    border-radius: none;
}
.average__value {
    font-size: 3.5em;
    font-weight: bolder;
}
.average__value-desc {
    font-weight: normal;
    font-size: 2em;
}
.average__block-info {
    padding-top: 10px;
    font-size: 2em;
}
.average__block-elem {
    display: flex;
    justify-content: space-between;
    gap: 10px;
}

.average__block-elem > div:first-child {
    color: #94c6dd;
}

.average__block-elem > div:last-child {
    font-weight: bolder;
}
</style>