import { watch, ref, onMounted } from 'vue';

export default function useModal(selectDate) {
    const fullnameCalendar = ref('')

    const getFullNameCalendar = () => {
        fullnameCalendar.value = new Date(selectDate.value).toLocaleString('en-Us', {
            weekday: "long",
            day: "numeric",
            month: "long",
            year: "numeric",
        }) + '.'
    }

    onMounted(getFullNameCalendar)
    watch(selectDate, getFullNameCalendar)

    return {
        fullnameCalendar,
        getFullNameCalendar
    }
}