<template>
    <div class="modal" @click="selectBar ? selectBar = false : null">
        <div class="modal__title">
                <div class="title">
                    <i :class="`fa-${icon}`" class="fa-solid"></i>
                    {{ titleModal }}
                </div>
            <div class="close">
                <div class="close__modal" @click="$emit('closeModal')">
                    <i class="fa-solid fa-xmark"></i>
                </div>
            </div>
        </div>
        <ModalMoon 
            v-if="titleModal === 'Moon'"
            :dataForChart="dataForChart"/>
        <div class="modal__sun" v-else-if="titleModal === 'Sun'"></div>
        <div class="modal__average" v-else-if="titleModal === 'Average values'"></div>
        <div class="modal__standart" v-else>
            <div class="modal__container">
                <div class="modal__calendar">
                    <CalendarElem 
                        v-for="(day) in weathers" 
                        :selectDate="selectDate" 
                        :today="today.datetime" 
                        :day="day.datetime" 
                        :key="day.datetime"
                        @click="changeDate(day.datetime)" 
                        />
                </div>
                <div class="calendar__fullname">
                        {{ fullnameCalendar }}
                </div>
                <hr>
                <div class="modal__description">
                    <div class="modal__bar">
                        <div :style="{
                            backgroundColor: selectBar ? '#31302e': null,
                            color: selectBar ? '#4a484485': null,
                        }"  class="select" @click.stop="selectBar = !selectBar">
                            <i :class="`fa-${icon}`" class="fa-solid"></i>
                            <i class="fa-solid fa-chevron-down"></i>
                        </div>
                        <Transition name="bounce">
                            <div class="select__bar" v-if="selectBar">
                                <MyModalSelect 
                                    v-for="menu in selectMenu"
                                    :menu="menu"
                                    :icon="icon"
                                    :key="menu.id" 
                                    @click="changeOptions(menu.name, menu.icon, menu.id)"/>
                            </div>
                        </Transition>
                    </div>
                    <div class="modal__specification">
                        <div
                        v-for="chart in selectMenu[idMenu - 1].chart" 
                        :key="chart.id"
                        :chart="chart"
                        >    
                            <TitleBarChart 
                            :dataForChart="dataForChart"
                            :chart="chart"
                            :icon="icon"
                            :hour="hour"/>
                            <BarChart
                            :chart="chart"
                            :today="dataForChart"
                            />
                        </div>
                        <div class="modal__menu-description">
                            <ModalMenuDescription 
                            v-for="menuDesc in selectMenu[idMenu - 1].menuDescription"
                            :title="menuDesc.title"
                            :body="menuDesc.body"
                            :key="menuDesc.id"/>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import BarChart from '@/components/modalView/BarChart.vue'
import CalendarElem from '@/components/modalView/CalendarElem.vue'
import MyModalSelect from '@/components/modalView/MyModalSelect.vue'
import TitleBarChart from '@/components/modalView/TitleBarChart.vue'
import ModalMoon from '@/components/modalView/ModalMoon.vue'
import ModalMenuDescription from '@/components/modalView/ModalMenuDescription.vue'
import {toRefs, ref, watch} from 'vue'
import useModal from '@/hooks/useModal'

    export default {
        components: {
            CalendarElem,
            MyModalSelect,
            BarChart,
            TitleBarChart,
            ModalMoon,
            ModalMenuDescription
        },
        name: 'my-modal',
        props: {
            today: {
                type: Object,
                required: true
            },
            titleModal: {
                type: String,
                required: true
            },
            icon: {
                type: String,
                required: true
            },
            weathers: {
                type: Array,
                required: true,
            },
            selectDate: {
                type: String,
            },
            hour: {
                type: Number
            }
        },
        setup(props) {
            const {selectDate, icon} = toRefs(props)
            const selectMenu = [
                    {id: 1, name: 'Weather', icon: 'cloud', menuDescription: [
                        {id: 1, title: 'Main', body: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quisquam amet explicabo sequi omnis, blanditiis officiis ad culpa laudantium. Repellendus rem velit temporibus ut iure quia doloribus, corporis in quod labore.'},
                    ], chart: [
                        {id: 1, title: '', configName: 'weather', prop: 'temp', unit: ''},
                        {id: 2, title: 'Chance of precipitation', configName: 'precipprob', prop: 'precipprob', unit: '%'},
                    ]},
                    {id: 2, name: 'UV-index', icon: 'sun', menuDescription: [
                        {id: 1, title: 'Comparison by day', body: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quisquam amet explicabo sequi omnis, blanditiis officiis ad culpa laudantium. Repellendus rem velit temporibus ut iure quia doloribus, corporis in quod labore.'},    
                        {id: 2, title: 'About the UV Index', body: 'The UV Index (UVI), developed by the World Health Organization, measures ultraviolet radiation. The higher the UV index, the greater the likelihood of harm to health and the faster it can occur. The UV index can help you decide when to protect yourself from the sun and when to stay away from the sun. WHO recommends staying in the shade, using sunscreen, hats and protective clothing at level 3 (moderate) and above.'},
                    ], chart: [
                        {id: 1, title: '', configName: 'uvindex', prop: 'uvindex', unit: ''},
                    ]},
                    {id: 3, name: 'Wind', icon: 'wind', menuDescription: [
                        {id: 1, title: 'Main', body: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quisquam amet explicabo sequi omnis, blanditiis officiis ad culpa laudantium. Repellendus rem velit temporibus ut iure quia doloribus, corporis in quod labore.'},
                        {id: 2, title: 'About speed and gusts of wind', body: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quisquam amet explicabo sequi omnis, blanditiis officiis ad culpa laudantium. Repellendus rem velit temporibus ut iure quia doloribus, corporis in quod labore.'},
                        {id: 3, title: 'Beaufort scale', body: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quisquam amet explicabo sequi omnis, blanditiis officiis ad culpa laudantium. Repellendus rem velit temporibus ut iure quia doloribus, corporis in quod labore.'},
                        {id: 4, title: 'About the beaufort scale', body: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quisquam amet explicabo sequi omnis, blanditiis officiis ad culpa laudantium. Repellendus rem velit temporibus ut iure quia doloribus, corporis in quod labore.'},
                    ], chart: [
                        {id: 1, title: '', configName: 'wind', prop: 'windspeed', unit: 'km/h'},
                    ]},
                    {id: 4, name: 'Precipitation', icon: 'droplet', menuDescription: [
                        {id: 1, title: 'Main', body: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quisquam amet explicabo sequi omnis, blanditiis officiis ad culpa laudantium. Repellendus rem velit temporibus ut iure quia doloribus, corporis in quod labore.'},
                    ], chart: [
                        {id: 1, title: '', configName: 'precipitation', prop: 'precip', unit: 'mm', icon: false},
                    ]},
                    {id: 5, name: 'Feels like', icon: 'temperature-three-quarters', menuDescription: [
                        {id: 1, title: 'Main', body: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quisquam amet explicabo sequi omnis, blanditiis officiis ad culpa laudantium. Repellendus rem velit temporibus ut iure quia doloribus, corporis in quod labore.'},
                        {id: 2, title: 'About the "Feels like" temperature indicator', body: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quisquam amet explicabo sequi omnis, blanditiis officiis ad culpa laudantium. Repellendus rem velit temporibus ut iure quia doloribus, corporis in quod labore.'},
                    ], chart: [
                        {id: 1, title: '', configName: 'feelslike', prop: 'feelslike', unit: ''},
                    ]},
                    {id: 6, name: 'Humidity', icon: 'water', menuDescription: [
                        {id: 1, title: 'Main', body: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quisquam amet explicabo sequi omnis, blanditiis officiis ad culpa laudantium. Repellendus rem velit temporibus ut iure quia doloribus, corporis in quod labore.'},
                        {id: 2, title: 'About relative humidity', body: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quisquam amet explicabo sequi omnis, blanditiis officiis ad culpa laudantium. Repellendus rem velit temporibus ut iure quia doloribus, corporis in quod labore.'},
                        {id: 3, title: 'About dew point', body: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quisquam amet explicabo sequi omnis, blanditiis officiis ad culpa laudantium. Repellendus rem velit temporibus ut iure quia doloribus, corporis in quod labore.'}               
                    ], chart: [
                        {id: 1, title: '', configName: 'humidity', prop: 'humidity', unit: '%'},
                    ]},
                    {id: 7, name: 'Visibility', icon: 'eye', menuDescription: [
                        {id: 1, title: 'Main', body: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quisquam amet explicabo sequi omnis, blanditiis officiis ad culpa laudantium. Repellendus rem velit temporibus ut iure quia doloribus, corporis in quod labore.'},
                        {id: 2, title: 'About visibility', body: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quisquam amet explicabo sequi omnis, blanditiis officiis ad culpa laudantium. Repellendus rem velit temporibus ut iure quia doloribus, corporis in quod labore.'},
                    ], chart: [
                        {id: 1, title: '', configName: 'visibility', prop: 'visibility', unit: 'km'},
                    ]},             
                    {id: 8, name: 'Pressure', icon: 'gauge', menuDescription: [
                        {id: 1, title: 'Main', body: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quisquam amet explicabo sequi omnis, blanditiis officiis ad culpa laudantium. Repellendus rem velit temporibus ut iure quia doloribus, corporis in quod labore.'},
                        {id: 2, title: 'About pressure', body: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quisquam amet explicabo sequi omnis, blanditiis officiis ad culpa laudantium. Repellendus rem velit temporibus ut iure quia doloribus, corporis in quod labore.'},
                    ], chart: [
                        {id: 1, title: '', configName: 'pressure', prop: 'pressure', unit: 'hPa'},
                    ]},
                ]
            const idMenu = ref(selectMenu.find(item => item.icon === icon.value) ? selectMenu.find(item => item.icon === icon.value).id : 0)

            const {fullnameCalendar, getFullNameCalendar} = useModal(selectDate);
        
            return {
                fullnameCalendar,
                getFullNameCalendar,
                idMenu,
                selectMenu,
            }
        },
        data() {
            return {
                selectBar: false,
            }
        },
        computed: {
            dataForChart() {
                return this.weathers[Math.abs(new Date(this.selectDate).getDate() - new Date().getDate())]
            },
        },
        methods: {
            changeDate(date, index) {
                this.$emit('changeDate', date)
            },
            changeOptions(name, icon, id) {
                this.idMenu = id
                this.selectBar = false
                this.$emit('changeOptions', name, icon);
            },

        },
    }
</script>

<style scoped>
.modal {
    z-index: 1;
    background-color: #1c1c1e;
    color: white;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 20px;
    left: 0;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    overflow-y: auto;
}

.modal__title {
    z-index: 5;
    padding: 20px;
    position: sticky;
    top: 0px;
    text-align: center;
    font-size: 4em;
    font-weight: bolder;
    background-color: #1c1c1eed;
    display: flex;
    align-items: center;
    backdrop-filter: blur(10px);
}

.modal__bar {
    display: flex;
    flex-direction: column;
    align-items: self-end;
    padding: 15px;
    position: relative;
}

.title {
    flex-grow: 30;
}

.close {
    flex-grow: 1;
}

.close__modal {
    background-color: #39383d;
    padding: 10px 5px;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-size: 0.7em;
}

.modal__calendar{
    display: flex;
    justify-content: space-between;
    text-align: center;
    gap: 20px;
    font-size: 3em;
    font-weight: bolder;
    overflow-x: auto;
    scroll-snap-type: x proximity;
    scrollbar-width: none; /* firefox */
    margin-bottom: 10px;
}

.modal__calendar::-webkit-scrollbar { 
    width: 0;
    height: 0;
}



hr {
    margin: 0;
    color: #2a292b;
    background-color: #2a292b;
}

.calendar__fullname {
    text-align: center;
    font-size: 3em;
    margin-bottom: 10px;
}

.select {
    transition: all 0.1s;
    background-color: #39383d;
    border-radius: 10px;
    cursor: pointer;
    display: flex;
    padding: 8px 0 8px 12px;
    align-items: center;
    width: max-content;
    position: relative;
}

.select i{
    font-size: 2em;
}

.select i:last-child{
    padding: 0 8px;
}

.select__bar {
    transform-origin: 90% top;
    z-index: 4;
    position: absolute;
    top: 90%;
    right: 1%;
    font-size: 2em;
    width: 30%;
    height: max-content;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: #39383d;
}

.modal__description {
    background: linear-gradient(1turn,#c4c4c4, #c4c4c4 50%, #39383d 80%, #1c1c1e 100%);
}

.modal__specification {
    margin-top: -60px;
    font-size: 5em;
    padding: 10px 10px 30px 10px;
}
.modal__menu-description {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.bounce-enter-active {
  animation: bounce-in 0.25s;
}
.bounce-leave-active {
  animation: bounce-in 0.25s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@media screen and (max-width: 430px) {
    .calendar__fullname {
        font-size: 2.1em;
    }
    .select__bar {
        width: 50vw;
    }
    .close__modal {
        padding: 5px;
    }
}

@media screen and (max-width: 330px) {
    .modal__specification {
        font-size: 4em;
    }

    .modal__title {
        font-size: 3em;
    }
    .modal__calendar {
        font-size: 2em;
        gap: 5px;
    }

   
}
</style>