<template>
    <div class="precipitation">
        <my-title><i class="fa-solid fa-droplet"></i> precipitaton</my-title>
        <div class="own__wrapper">
            <div class="precipitation__value">{{ today.precip }} mm</div>
            <div class="precipitation__descrip-value">in the last 24 hours</div>
            <div class="precipitation__description">{{ tomorrowPrecip ? 'Expected tomorrow ' + tomorrowPrecip.join(', ') : 'No precipitation tomorrow' }}</div>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            today: {
                type: Object,
                required: true
            },
            tomorrowPrecip: {
                type: [Object, null],
                required: true
            }
        },
    }
</script>

<style scoped>

.precipitation__value {
    font-size: 3em;
    font-weight: bolder;
}
.precipitation__descrip-value {
    font-size: 2em;
    font-weight: bolder;
}
.precipitation__description {
    font-size: 1.5em;
}
</style>