import { onBeforeMount, onMounted, ref } from "vue";

export default function useListenerScroll() {
    const heightTitleShowed = ref(50)
    const opacity = ref(1000)
    const listener = () => {
        document.addEventListener('scroll', () => {
            const scrollHourMenuY = document.querySelector('.scroll-hour-menu')
            const titleShowed = document.querySelector('.title__showed')
            if (scrollHourMenuY && titleShowed) {
                opacity.value = scrollHourMenuY.getBoundingClientRect().y
                heightTitleShowed.value = titleShowed.offsetHeight
            }
        });
    }

    onBeforeMount(listener)

    return {
        listener,
        opacity,
        heightTitleShowed
    }
}