<template>
    <div>
        <i v-if="values[count * 2 - 1] > values[count * 2  - 2]" class="fa-sharp fa-solid fa-arrow-up fa-2xs"></i>
        <i v-else-if="values[count * 2 - 1] < values[count * 2 - 2]" class="fa-sharp fa-solid fa-arrow-down fa-2xs"></i>
        <i v-else class="fa-solid fa-equals fa-2xs"></i>
    </div>
</template>

<script>
    export default {
        props: {
            values: {
                type: Array,
                required: true
            },
            count: {
                type: Number,
            }
        }
    }
</script>

