<template>
    <div class="title" :style="{top: heightTitle - 1 + 'px'}">
        <div class="title__wrapper">
            <slot></slot>
        </div>
    </div>
</template>

<script>
import { ref } from 'vue'
export default {
        name: 'my-title',
        setup(props) {
            const heightTitle = ref(50)

            return {
                heightTitle
            }
        },
        methods: {
            checkHeight() {
                document.addEventListener('scroll', () => {
                    const titleShowed = document.querySelector('.title__showed')
                    if (titleShowed) {
                        this.heightTitle = titleShowed.offsetHeight
                    }
                })
            }
        },
        mounted() {
            this.checkHeight()
        }
    }
</script>

<style  scoped>
.title {
    background: linear-gradient(0.25turn, #2E9AFF, #29b8ff);
    position: sticky;
    z-index: 1;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;   
}
.title__wrapper {
    border-radius: 10px;
    border: transparent;
    background: #3272b1; 
    backdrop-filter: blur(1px);
    padding: 10px ;
    font-size: 1.5em;
    text-transform: uppercase;
    color: #94c6dd;
    z-index: 1;        
}
</style>