<template>
    <div class="calendar__elem">
        <div class="calendar__name">{{ new Date(day).toLocaleString('en-Us', {weekday: "short"}).slice(0,1) }}</div>
        <button
            :class="{
                btnToday: day === today,
                btnActive: selectDate === day && day !== today,
                btnTodayActive: selectDate === day && day === today,
            }"
            class="calendar__number btn">
                {{ new Date(day).getDate() }}
        </button>
    </div>
</template>

<script>
export default {
    props: {
        day: {
            type: String,
            required: true
        },
        today: {
            type: String,
            required: true
        },
        selectDate: {
            type: String,
        }
    },
}
</script>

<style scoped>
button {
    border: none;
    background: transparent;
    color: white;
}

.btnToday {
    color: #67d1fb;
}
.btnActive {
    background-color: white;
    color:#1c1c1e
}

.btnTodayActive {
    background-color: #67d1fb;
    color: #1c1c1e;
}

.calendar__number {
    padding: 10px;
    border-radius: 100%;
    cursor: pointer;
    font-size: 1em;
    transition: all 0.5s;
    width: 55px;
    height: 55px;
    display: flex; 
    justify-content: center;
    align-items: center;
}

.calendar__name { 
    margin-bottom: 10px;
}

.calendar__elem {
    padding: 0 10px;
}

@media screen and (max-width: 430px) {
    .calendar__number {
        width: 35px;
        height: 35px;
    }
}

@media screen and (max-width: 330px) {
    .calendar__number {
        width: 20px;
        height: 20px;
    }
}

</style>