<template>
    <div class="scroll-hour-menu-bar__elem">
        <div v-if="typeof hour === 'object'">
            <div class="scroll-hour-menu-bar__time">{{ hour.datetime.slice(0,2) }}</div>
            <div class="scroll-hour-menu-icon" >
                <img :src="require(`@/asserts/img/WeatherIcons-main/${hour.icon}.png`)" :alt="hour.icon">
            </div>
            <div class="scroll-hour-menu-bar__temp">{{ Math.round(hour.temp) }}&deg;</div>
        </div>
        <div v-else>
            <div class="scroll-hour-menu-bar__time">{{ hour.slice(0, 5) }}</div>
            <div class="scroll-hour-menu-icon sun-icon" >
                <img width="40" height="40" :src="require(`@/asserts/img/WeatherIcons-main/${+hour.slice(0,2) < 12 ? 'sunrise' : 'sunset'}.png`)" :alt="hour.icon">
            </div>
            <div class="scroll-hour-menu-bar__name">
                <span v-if="+hour.slice(0,2) < 12">Sunrise</span>
                <span v-else>Sunset</span>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            hour: {
                type: [Object, String],
                required: true
            }
        }
    }
</script>

<style scoped>

.scroll-hour-menu-bar__elem:first-child {
    margin-left: 10px;
}

.scroll-hour-menu-bar__elem:last-child {
    margin-right: 10px;
}
.scroll-hour-menu-bar__elem {
    padding-top: 5px;
    min-width: 5%
}
.scroll-hour-menu-icon {
    position: relative;
    padding-top: 100%;
}

.scroll-hour-menu-icon img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: auto;
}

.sun-icon {
    width: 65%;
    margin: auto;
}

.sun-icon img{
    top: 15%;
}
</style>