<template>
    <div class="scroll-hour-menu">
        <my-title class="scroll-hour-menu__title">
            <span style="color: white" 
            :style="{
                opacity:((+opacity.toFixed(2) / 100) - 0.5).toFixed(2),
                display: opacity < 50 ? 'none' : 'block'
            }">{{ today.description }}</span>
            <span 
            :style="{
                opacity: (0.5 -(+opacity.toFixed(2) / 100)).toFixed(2),
                display: opacity < 50 ? 'block' : 'none'
            }"><i class="fa-regular fa-clock"></i> Hourly forecast</span>
        </my-title>
        <div class="scroll-hour-menu__wrapper" >
            <hr>
            <div class="scroll-hour-menu-bar" >
                <ScrollHourMenuBarItem 
                    v-for="hour in concatHourTwoDays" 
                    :key="hour.datetimeEpoch" 
                    :hour="hour"/>
            </div>
        </div>
    </div>
</template>

<script>
import useConcatHourTwoDays from '@/hooks/useConcatHourTwoDays'
import ScrollHourMenuBarItem from '@/components/basicView/ScrollHourMenuBarItem.vue'
    
    export default {
        components: {
            ScrollHourMenuBarItem
        },  
        props: {
            today: {
                type: Object,
                required: true
            },
            tomorrow: {
                type: Object,
                required: true
            },
            opacity: {
                type: Number,
                required: true
            }
        },

        setup(props) {
            const { concatHourTwoDays } = useConcatHourTwoDays(props.today, props.tomorrow)
            return {
                concatHourTwoDays
            }
        },
    }
</script>

<style scoped>

.scroll-hour-menu__wrapper {
    padding-bottom: 10px;
}

.scroll-hour-menu-bar {
    display: flex;
    justify-content: space-between;
    text-align: center;
    gap: 20px;
    font-size: 1.5em;
    font-weight: bolder;
    overflow-x: auto;
    scroll-snap-type: x proximity;
    scrollbar-width: none; /* firefox */
}

.scroll-hour-menu-bar::-webkit-scrollbar { /* webkit */
    width: 0;
    height: 0;
}

@media screen and (max-width: 330px) {
    .scroll-hour-menu-bar__elem {
        min-width: 7%;
    };
    .scroll-hour-menu-icon {
        width: 6vw;
    }
}

</style>