<template>
    <div class="moon">
        <my-title><i class="fa-regular fa-moon"></i> moon</my-title>
        <div class="own__wrapper">
            <div class="moon-block__elem">
                <span>Illumination </span>
                <span>{{ Math.round(Illumination) }} %</span>
            </div>
            <hr>
            <div class="moon-block__elem">
                <span>{{ moon.state }} </span>
                <span>{{ moon.time }}</span>
            </div>
            <hr>
            <div class="moon-block__elem">
                <span>Next Full Moon </span>
                <span>in {{ nextMoon }} days.</span>
            </div>
        </div>
    </div>
</template>

<script>
import useMoon from '@/hooks/useMoon'
    export default {
        props: {
            today: {
                type: Object,
                required: true
            },
            tomorrow: {
                type: Object,
                required: true
            },
            hour: {
                type: Number,
                default: new Date().getHours()
            }
        },
        setup(props) {
            const {moon, Illumination, nextMoon} = useMoon(props.today, props.tomorrow, props.hour)

            return {
                moon,
                Illumination,
                nextMoon
            }
        },
    }
</script>

<style scoped>

.moon hr {
    margin: 0;
}

.moon-block__elem {
    display: flex;
    justify-content: space-between;
    font-size: 2em;
    font-weight: bolder;
    padding: 5px 0;
}

.moon-block__elem span:last-child{
    color: #94c6dd;
}
</style>