<template>
    <div class="uv-index">
        <my-title>
            <i class="fa-regular fa-sun"></i> uv-index
        </my-title>
        <div class="own__wrapper">
            <div class="uv-index__elem">
                <div class="uv-index__value">{{ today.uvindex }}</div>
                <div class="uv-index__subtitle"> {{ uvSubtitle }}</div>
            </div>
            <div class="uv-index__bar">
                <div class="uv-index__bar-circle" :style="{
                    left: `${today.uvindex * 10}%`
                }"></div>
            </div>  
            <div class="uv-index__description">Will remain low until the end of the day</div>
        </div>  
    </div>
</template>

<script>
import useUVindex from '@/hooks/useUVindex';

    export default {
        props: {
            today: {
                type: Object,
                required: true
            }
        },
        setup(props) {
            const {uvIndex, uvSubtitle} = useUVindex(props.today)
            
            return {
                uvIndex, uvSubtitle
            }
        }
    }
</script>

<style scoped>
.uv-index {
    width: 50%;
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.uv-index__elem {
    font-size: 2.5em;
    font-weight: bolder;
}
.uv-index__bar {
    position: relative;
    min-height: 5px;
    border-radius: 5px;
    border: none;
    background: linear-gradient(90deg, lightgreen, yellow, orange, red, violet);
}

.uv-index__wrapper {
    padding: 0 10px 10px;
}

.uv-index__bar-circle {
    min-width: 8px;
    min-height: 8px;
    border-radius: 100%;
    background-color: white;
    border: 2px solid #3b76b0;
    position: absolute;
    top: -30%;
}

.uv-index__description {
    font-size: 2.5em;
}
</style>