<script>
import useWeather from '@/hooks/useWeather'
import Location from '@/components/basicView/Location.vue'

export default {
    components: {
        Location
    },
    data() {
        return {
            page: 0,
            allPages: 0
        }
    },
    setup(props) {
        const locations = ['Minsk']

        const parsed = JSON.stringify(locations);
        localStorage.setItem('locations', parsed);

        const {weatherCollection} = useWeather(locations)

        return {
            weatherCollection
        }
    },
    methods: {
        saveLocation() {
            const parsed = JSON.stringify(this.locations);
            localStorage.setItem('locations', parsed); 
        }
    },
    mounted() {
        if (localStorage.getItem('locations')) {
            try {
                this.locations = JSON.parse(localStorage.getItem('locations'));
            } catch(e) {
                localStorage.removeItem('locations');
            }
        }
    }
}
</script>

