<template>
    <div 
        :class="{
            weatherIcon: chart.configName === 'weather',
            uvindexIcon: chart.configName === 'uvindex',
            windIcon: chart.configName === 'wind',
            humidityIcon: chart.configName === 'humidity',
            visibilityIcon: chart.configName === 'visibility',
            pressureIcon: chart.configName === 'pressure'
        }">
        <NavbarWeather 
            class="img__wrapper-navbar-weather"
            v-if="chart.configName === 'weather' ||
                  chart.configName === 'precipitation' ||
                  chart.configName === 'feelslike'"
            v-for="(hour, index) in today.hours"
            v-show="index % 2 !== 0" 
            :key="hour"
            :hour="hour"  />
        <NavbarUVindex 
            class="img__wrapper-navbar-uvindex"
            v-else-if="chart.configName === 'uvindex'"
            v-for="count in 12"
            :count="count"
            :values="values"/>
        <NavbarWind 
            class="img__wrapper-navbar-wind"
            v-else-if="chart.configName === 'wind'"
            v-for="(dir, index) in today.hours"
            v-show="index % 2 !== 0" 
            :key="dir.id"
            :dir="dir"/>
        <NavbarHumidity 
            class="img__wrapper-navbar-humidity"
            v-else-if="chart.configName === 'humidity'"
            v-for="count in 4"
            :values="values"
            :count="count"/>
        <NavbarVisibility 
            class="img__wrapper-navbar-visibility"
            v-else-if="chart.configName === 'visibility'"
            v-for="count in 12"
            :values="values"
            :count="count"/>
        <NavbarPressure 
            v-else-if="chart.configName === 'pressure'"
            v-for="count in 12"
            class="img__wrapper-navbar-pressure"
            :values="values"
            :count="count"
            />
    </div>
</template>


<script>
  import NavbarPressure from '@/components/modalView/topNavbar/NavbarPressure'
  import NavbarHumidity from '@/components/modalView/topNavbar/NavbarHumidity.vue'
  import NavbarVisibility from '@/components/modalView/topNavbar/NavbarVisibility.vue'
  import NavbarUVindex from '@/components/modalView/topNavbar/NavbarUVindex.vue'
  import NavbarWind from '@/components/modalView/topNavbar/NavbarWind.vue'
  import NavbarWeather from '@/components/modalView/topNavbar/NavbarWeather.vue'
    export default {
        components: {
            NavbarPressure,
            NavbarHumidity,
            NavbarVisibility,
            NavbarUVindex,
            NavbarWind,
            NavbarWeather
        },
        props: {
            values: {
                type: Array,
                reuqired: true
            },
            chart: {
                type: Object,
                reqiured: true
            },
            today: {
                type: Object,
                reuqired: true
            }
        }
    }
</script>

<style scoped>
.img__wrapper-navbar-weather {
  width: 100%;
  position: relative;
  height: max-content 
}
.img__wrapper-navbar-pressure ,
.img__wrapper-navbar-uvindex ,
.img__wrapper-navbar-humidity ,

.img__wrapper-navbar-visibility {
    font-size: 0.5em;
    font-weight: bolder;
}

.uvindexIcon,
.pressureIcon,
.visibilityIcon,
.humidityIcon,
.windIcon {
    justify-content: space-between;
    gap: 10px;
}


.uvindexIcon {
    justify-content: space-between;
    padding: 0 40px 0 20px;
}

.humidityIcon {
    justify-content: space-around;
}

.pressureIcon {
    padding: 0 80px 0 20px;
}
</style>