<template>
    <div class="pressure">
        <my-title><i class="fa-solid fa-gauge"></i> pressure</my-title>
        <div class="pressure__wrapper own__wrapper">
            <div class="pressure__arrow-for-value">
                <i class="fa-solid fa-arrow-up" v-if="val > prev"></i>
                <i class="fa-solid fa-arrow-down" v-else-if="val < prev"></i>
                <i class="fa-solid fa-equals" v-else></i>
            </div>
            <div class="pressure__value">{{ Math.round(val)}}</div>
            <div class="pressure__description">hPa</div>
            <div class="circle__pressure"></div>
        </div>
        <div class="pressure__arrows">
                <i class="fa-solid fa-arrow-down" ></i>
                <i class="fa-solid fa-arrow-up"></i>
        </div>
    </div>
</template>

<script>
import { ref } from 'vue';

    export default {
        props: {
            today: {
                type: Object,
                required: true
            },
            hour: {
                type: Number,
                default: new Date().getHours()
            }
        },
        setup(props) {
            const prev = ref(0)
            const val = ref(0)
            prev.value = props.today.hours[props.hour ? props.hour - 1 : props.hour].pressure
            val.value = props.today.hours[props.hour].pressure

            return {
                prev,
                val
            }
        }
    }
</script>

<style scoped>
.pressure {
    display: flex;
    flex-direction: column;
    position: relative;
}
.pressure__wrapper {
    background-image: url('@/asserts/img/timer.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center -2px;
    align-items: center;
    font-size: 2em;
    font-weight: bolder;
    gap: 0px;
}

.pressure__arrow-for-value {
    margin-top: 10px;
    font-size: 1.5em;
}

.pressure__arrows {
    display: flex;
    justify-content: center;
    gap: 15%;
    font-size: 2em;
    padding-bottom: 10px;
}

.pressure__value {
    font-size: 2em;
}

.pressure__arrows i:first-child{
    align-self: flex-start;
}

.pressure__arrows i:last-child{
    align-self: flex-end;
}

@media screen and (max-width: 330px) {
    .pressure__wrapper {
        margin: 0 10px;
    }
}
</style>