<template>
    <div class="sun">
        <my-title class="sun__title">
            <i class="fa-solid fa-circle-chevron-up" v-if="true"></i>
            <i class="fa-solid fa-circle-chevron-down" v-else></i>
            {{ sun.stateOne.state }}
        </my-title>
        <div class="own__wrapper">
            <div class="sun__time">
                {{ sunRiseSet[0].slice(0, 5) }}
            </div>
            <div class="sun__diagramm"></div>
            <div class="sun__subtitle">
                {{ sun.stateTwo.state }}
                {{ sunRiseSet[1].slice(0, 5) }}
            </div>
        </div>
    </div>
</template>

<script>
import useConcatHourTwoDays from '@/hooks/useConcatHourTwoDays';
import useSun from '@/hooks/useSun';

    export default {
        props: {
            today: {
                type: Object,
                required: true
            },
            tomorrow: {
                type: Object,
                required: true
            }
        },
        setup(props) {
            const {concatHourTwoDays} = useConcatHourTwoDays(props.today, props.tomorrow)
            const {sunRiseSet, sun} = useSun(concatHourTwoDays.value)

            return {
                sunRiseSet,
                sun
            }
        }

    }
</script>

<style scoped>
.sun {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.sun__time {
    font-size: 2.5em;
    font-weight: bolder;
}
.sun__subtitle {
    font-size: 2em;
}
</style>