<template>
    <div class="visibility">
        <my-title><i class="fa-solid fa-eye"></i> visibility</my-title>
        <div class="own__wrapper">
            <div class="visibility__value">{{ Math.round(today.visibility) }} km</div>
            <div class="visibility__description">{{ descr }}</div>
        </div>
    </div>
</template>

<script>
import useVisibility from '@/hooks/useVisibility'
    export default {
        props: {
            today: {
                type: Object,
                required: true
            }
        },
        setup(props) {
            const {visibilityDesc, descr} = useVisibility(props.today)
            
            return {
                visibilityDesc,
                descr
            }
        }
    }
</script>

<style scoped>
.visibility__value {
    font-size: 3em;
    font-weight: bolder;
}
.visibility__description {
    font-size: 2em;
}
</style>