<template>
    <div class="app__show" 
        v-if="weatherLoad"
        :style="{
            margin: selectedModal? `${yBody + 10}px 10px 10px 10px` : '0',
             'border-radius': selectedModal? '10px': '0px',
        }">
            <div class="title__showed">
                <div>{{ weatherProps.address }}</div>
                <div class="title__disabled" :style="{opacity: -(-1.2 + (+opacity.toFixed(2) / 100)).toFixed(2)}" >
                    {{ Math.round(today.temp) }}&deg; | {{ today.conditions }}
                </div>
            </div>
            <MyMain
                :opacity="opacity" 
                :today="today" 
                :heightTitleShowed="heightTitleShowed" 
                class="my-main" />
            <div class="container" >
                <ScrollHourMenu 
                    @click="openModal('Weather', 'cloud')" 
                    class="back" 
                    :today="today" 
                    :tomorrow="weathers[1]" 
                    :opacity="opacity"/>
                <TenDayForecast 
                    @check="openModal" 
                    class="back" 
                    :weathers="weathers"/>
                <div class="precipitation-map back">
                    <my-title class="precipitation-map__title">
                        <i class="fa-solid fa-umbrella"></i> precipitation
                    </my-title>
                    <div class="precipitation-map-block">
                        <iframe src="https://embed.windy.com/embed2.html?lat=53.901&lon=27.571&detailLat=53.901&detailLon=27.571&width=650&height=450&zoom=5&level=surface&overlay=wind&product=ecmwf&menu=&message=&marker=&calendar=now&pressure=&type=map&location=coordinates&detail=&metricWind=default&metricTemp=default&radarRange=-1" frameborder="0"></iframe>
                    </div>
                </div>
                <div class="block__elem">
                    <UvIndex 
                        class="back" 
                        :today="today.hours[hour]" 
                        @click="openModal('UV-index', 'sun')"/>
                    <Sunset 
                        class="back" 
                        :today="today"
                        :tomorrow="weathers[1]"/>
                </div>
                <Wind 
                    class="back" 
                    :today="today.hours[hour]" 
                    @click="openModal('Wind', 'wind')"/>
                <div class="block__elem"> 
                    <FeelsLike 
                        class="back" 
                        :today="today.hours[hour]" 
                        @click="openModal('Feels like', 'temperature-three-quarters')"/>
                    <Precipitation 
                        class="back" 
                        :today="today" 
                        :tomorrowPrecip="weathers[1].preciptype" 
                        @click="openModal('Precipitation', 'droplet')"/>
                </div>
                <div class="block__elem">
                    <Visibility 
                        class="back" 
                        :today="today.hours[hour]" 
                        @click="openModal('Visibility', 'eye')"/>
                    <Humidity 
                        class="back" 
                        :today="today.hours[hour]" 
                        @click="openModal('Humidity', 'water')"/>
                </div>
                <Moon 
                    class="back" 
                    :hour="hour" 
                    :tomorrow="weathers[1]" 
                    :today="today" 
                    @click="openModal('Moon', 'moon')"/>
                <div class="block__elem">
                    <Average 
                        class="back" 
                        :today="today"/>
                    <Pressure 
                        class="back" 
                        :hour="hour" 
                        :today="today" 
                        @click="openModal('Pressure', 'gauge')"/>
                </div>
            </div>
            <!-- <div class="app__bottom">
                <div class="bottom__wrapper">
                    <div class="bottom__elem bottom__map">
                        <i class="fa-regular fa-map"></i>
                    </div>
                    <div class="bottom__elem bottom__page">
                        <div class="page__elem" v-for="i in 2" @click="console.log(i)">
                            <i v-if="true"  class="fa-solid fa-circle"></i>
                            <i v-else class="fa-regular fa-circle"></i>
                        </div>
                    </div>
                    <div class="bottom__elem bottom__modal" >
                        <i class="fa-solid fa-bars"></i>
                    </div>
                </div>
            </div> -->
            <Transition :duration="550" name="nested">
                <my-modal
                    v-if="selectedModal"
                    :today="today" 
                    :titleModal="titleModal" 
                    :weathers="weathers"
                    :icon="icon"
                    :hour="hour"
                    :selectDate="selectDate || today.datetime"
                    @closeModal="closeModal"
                    @changeOptions="changeOptions"
                    @changeDate="changeDate"
                    class="outer"
                    :style="{
                        top: `${yBody + 20}px`
                    }"
                    />
        </Transition>
        </div>
</template>

<script>
import MyMain from '@/components/basicView/MyMain.vue'
import ScrollHourMenu from '@/components/basicView/ScrollHourMenu.vue'
import TenDayForecast from '@/components/basicView/TenDayForecast.vue'
import Sunset from '@/components/basicView/Sunset.vue'
import UvIndex from '@/components/basicView/UvIndex.vue'
import Wind from '@/components/basicView/Wind.vue'
import Precipitation from '@/components/basicView/Precipitation.vue'
import FeelsLike from '@/components/basicView/FeelsLike.vue'
import Visibility from '@/components/basicView/Visibility.vue'
import Humidity from '@/components/basicView/Humidity.vue'
import Moon from '@/components/basicView/Moon.vue'
import Pressure from '@/components/basicView/Pressure.vue'
import Average from '@/components/basicView/Average.vue'
import useFetchWeather from  '@/hooks/useFetchWeather'
import useListenerScroll from '@/hooks/useListenerScroll'
import useOCModal from '@/hooks/useOCModal'
import MyModal from '@/components/modalView/MyModal.vue'

import {toRefs} from 'vue'
    export default {
        props: {
            weather: {
                type: Object,
                required: true
            }
        },
        components: {
            MyMain,
            ScrollHourMenu,
            TenDayForecast,
            UvIndex,
            Sunset,
            Wind,
            FeelsLike,
            Precipitation,
            Visibility,
            Humidity,
            Moon,
            Pressure,
            Average,
            MyModal
        },
        setup(props) {
            const {weather} = toRefs(props)
            const { weatherProps, weathers, weatherLoad, today } = useFetchWeather(weather.value)
            const {listener, opacity, heightTitleShowed} = useListenerScroll()
            const {openModal, closeModal, changeOptions, changeDate, selectedModal, titleModal, icon, selectDate, yBody} = useOCModal()
            return {
                weatherProps, weathers, weatherLoad, today,
                listener, opacity, heightTitleShowed,
                openModal, closeModal, changeOptions, changeDate, selectedModal, titleModal, icon, selectDate, yBody
            }
        },
        data() {
            return {
                hour: new Date().getHours(),
            }
        },
    }
</script>

<style scoped>
.app__show {
    display: flex;
    flex-direction: column;
    gap: 10px;
    background: linear-gradient(0.25turn, #2E9AFF, #29b8ff);
    backdrop-filter: blur();
    padding: 15px;
    transition: all 0.3s;
}

.title__showed {
    padding: 20px 0 10px;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    top: 0;
    text-align: center;
    position: sticky;
    height: max-content;
    z-index: 1;
    background: linear-gradient(0.25turn, #2E9AFF, #29b8ff);
    backdrop-filter: blur();
}

.title__showed div:first-child {
    font-size: 4em;
}

.title__disabled {
    font-size: 2em;
}

.back {
    border-radius: 10px;
    background: #3272b1; 
    backdrop-filter: blur(2px);
}

.precipitation-map-block {
    position: relative;
    padding-top: 100%;
    margin: 0 10px 10px;
}

.precipitation-map-block iframe{
    position: absolute;
    border: 0;
    height: 100%;
    left: 0; 
    top: 0;
    width: 100%;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;

}

.block__elem {
    display: flex;
    justify-content: space-between;
    gap: 10px;
}

.block__elem > div {
    width: 50%;
}

hr {
    border: none;
    color: rgba(60, 157, 248, 1);
    background-color: rgb(60, 157, 248);
    height: 1px;
    margin-left: 10px;
}

.container {
    display: flex;
    flex-direction: column;
    gap: 15px;
    position: relative;
}

.own__wrapper {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 0 10px 10px;
}
  
.nested-enter-active, .nested-leave-active {
	transition: all 0.3s ease-in-out;
}

.nested-leave-active {
  transition-delay: 0.25s;
}

.nested-enter-from,
.nested-leave-to {
  transform: translateY(2000px);
  opacity: 0;
}

@media screen and (max-width: 770px) {
    .app {
        font-size: 1vw;
    }
}

@media screen and (max-width: 375px) {
    .app {
        font-size: 2.2vw;
    }
}

.app__bottom {
    position: sticky;
    bottom: 0;
    padding: 10px 2px 20px;
    background: linear-gradient(0.25turn, #2e9aff85, #29b8ff7a);
    backdrop-filter: blur(10px);    
}
.bottom__wrapper {
    display: flex;
    display: flex;
    font-size: 2em;
    justify-content: space-between;
    gap: 10px;
    flex-grow: 1;
}

.bottom__wrapper i {
    cursor: pointer;
}

.bottom__elem {
}
.bottom__map i{
}

.bottom__page {
    display: flex;
}
.page__elem {
    padding: 0 5px;
}

.bottom__modal i{
}

.app-elem-enter-active {
  animation: bounce-in 0.5s;
}
.app-elem-leave-active {
  animation: bounce-in 0.5s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.25);
  }
  100% {
    transform: scale(1);
  }
}

</style>