<template>
    <div class="wind">
        <my-title class="wind__title">
            <i class="fa-solid fa-wind"></i> wind
        </my-title>
        <div class="wind__main">
            <div class="wind__block">
                <div class="wind__elem">
                    <div class="wind__value">{{ Math.round(today.windspeed) }}</div>
                    <div class="wind__description">
                        <div class="wind__description-prop">km/h</div>
                        <div class="wind__description-mark">winds speed</div>
                    </div>
                </div>
                <hr>
                <div class="wind__elem">
                    <div class="wind__value">{{ Math.round(today.windgust) }}</div>
                    <div class="wind__description">
                        <div class="wind__description-prop">km/h</div>
                        <div class="wind__description-mark">wind gust</div>
                    </div>
                </div>
            </div>
            <div class="wind__compass">
                <div class="compass">
                    <img src="@/asserts/img/compass.png" alt="compass">
                    <div class="compass__wrapper" :style="{
                        transform: `rotate(${-winddir - 180}deg)`
                    }">
                        <i class="fa-solid fa-arrow-up"></i>
                    </div>
                    <div class="compass__circle">
                        {{ rumb.name }}
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import useWind from '@/hooks/useWind'
    export default {
        props: {
            today: {
                type: Object,
                required: true
            }
        },
        setup(props) {
            const {winddir, rumb} = useWind(props.today)
            
            return {
                winddir,
                rumb,
            }
        },
       
    }
</script>

<style scoped>

.wind__main {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-bottom: 10px;
}
.wind__block {
    font-size: 2em;
    width: 50%;
}
.wind__elem {
    display: flex;
    align-items: center;
    gap: 15px;
}
.wind__value {
    font-size: 3em;
    font-weight: bolder;
}
.wind__description-prop {
    color: #94c6dd;
}
.wind__description-mark {
    font-weight: bolder;

}
.wind__compass {
    width: 20%;
}

.compass {
    position: relative;
    padding-top: 100%;
} 
.compass img {
position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: auto;
}

.compass__wrapper {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
}

.compass__wrapper i::after {
    content: '';
    position: absolute;
    display: block;
    width: 20%;
    height: 700%;
    left: 40%;
    top: 20%;
    background-color: white;
}

.compass__circle {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    height: 45%;
    width: 45%;
    top: 27.5%;
    left: 27.5%;
    border-radius: 100%;
    background: rgba(41, 103, 160);
    backdrop-filter: blur(2px);
    box-shadow: 0px 0px 20px 0px black;
    font-size: 3em;
    font-weight: bolder;
    
}
.compass i{
    position: absolute;
    top: 10%;
    left: 45.5%;
    font-size: 2em;
}

@media screen and (max-width: 330px) {
    .wind__compass {
        width: 40%;
    }

    .compass__wrapper i::after {
        height: 570%;
    }
}
</style>