<template>
    <div class="feels-like">
        <my-title><i class="fa-solid fa-temperature-half"></i> Feels like</my-title>
        <div class="own__wrapper">
            <div class="feels-like__value">{{ Math.round(today.feelslike) }}&deg;</div>
            <div class="feels-like__description">{{ description }}</div>
        </div>    
    </div>
</template>

<script>
    export default {
        props: {
            today: {
                type: Object,
                required: true
            },
        },
        data() {
            return {
                descriptionFeels: [
                    'The wind makes it seem colder',
                    'Humidity makes it seem warmer',
                    'Feels like actual temperature'
                ]
            }
        },
        computed: {
            description() {
                if (this.today.feelslike < this.today.temp) return this.descriptionFeels[0]
                else if (this.today.feelslike > this.today.temp) return this.descriptionFeels[1]
                else return this.descriptionFeels[2]
            }
        },
     
    }
</script>

<style  scoped>
.feels-like__value {
    font-size: 3.5em;
    font-weight: bolder;
}
.feels-like__description {
    font-size: 2em;
}
</style>